@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --light-grey: #EDF2F4;
  --grey: #8083a2;
  --dark-grey: #2B2D42;
  --light-red: #EF233C;
  --dark-red: #D90429;
  --clr-primary: #2978b5;
  --shadow: rgba(100,100,111,0.2) 0px 7px 29px 0px;
}

html {
  scroll-behavior: smooth;
  background-color: var(--light-grey);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Global Styles */
.revealUp {
  opacity: 0;
  visibility: hidden;
}

.container1 {
  width: 70%;
  margin: auto;
}

@media screen and (max-width: 992px) {
  .container1 {
    width: 85%;
  }
}

@media screen and (max-width: 768px) {
  .container1 {
    width: 90%;
  }
}

@media screen and (max-width: 480px) {
  .container1 {
    width: 95%;
  }
}

.center {
  display: flex;
  justify-content: center;
}

.break {
  flex-basis: 100%;
  height: -1;
}

.home_item {
  width: 100%;
}

.center-text {
  text-align: center;
}

/* Typography */
h3 {
  font-size: 2rem;
  margin: 0 !important;
  font-weight: 700;
}

@media screen and (max-width: 768px) {
  h3 {
    font-size: 1.75rem;
  }

  .pb-4 {
    padding-bottom: 1.5rem !important;
  }
}

@media screen and (max-width: 480px) {
  h3 {
    font-size: 1.5rem;
  }
}

/* Header & Navigation */
header {
  top: 0;
  display: flex !important;
  align-items: center;
  justify-content: space-between;
  width: 95%;
  max-width: 1100px;
  margin: 0 auto;
  padding: 1rem 0;
}

header h3 {
  color: var(--clr-primary);
  font-weight: bold;
}

header::after {
  content: "";
  display: table;
  clear: both;
}

.navigation {
  margin-left: auto;
}

nav {
  float: right;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  display: inline-block;
  margin-left: 6vw;
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;
}

header a {
  color: var(--dark-grey);
  text-decoration: none !important;
  text-transform: none;
}

a {
  text-decoration: none !important;
}

header a:hover {
  color: var(--clr-primary);
}

nav a::before {
  content: "";
  display: block;
  height: 5px;
  width: 0%;
  background-color: var(--dark-grey);
  position: absolute;
  top: 0;
  transition: all ease-in-out 150ms;
}

nav a:hover::before {
  width: 100%;
}

@media screen and (max-width: 768px) {
  header {
    padding: 0.8rem 1rem;
  }

  nav ul {
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
  }

  nav li {
    margin-left: 0;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  header h3 {
    font-size: 1.3rem;
  }

  nav a {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 480px) {
  nav ul {
    gap: 1.2rem;
  }

  nav a {
    font-size: 1.4rem;
    padding: 0.5rem 0.3rem;
  }

  header h3 {
    font-size: 1.1rem;
  }
}

/* Home Page */
.page-size {
  background-size: cover;
  background-position: center;
  height: 94vh;
  padding: 0 1rem;
}

.title {
  text-align: center;
  align-items: center;
  height: 100%;
  width: 100%;
  display: flex;
}

.title .home_name {
  color: var(--clr-primary);
  font-size: min(10vw, 5.5rem);
  font-weight: bold;
  overflow: hidden;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: .01em;
  width: fit-content;
}

.title h2 {
  color: var(--dark-grey);
  font-size: min(3rem,9vw);
  margin-bottom: 0;
}

.fa {
  padding: 20px;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 50%;
  transition: all .5s ease !important;
}

.fa:hover {
  opacity: 0.7;
  color: var(--light-grey);
  transition: all .5s ease !important;
}

.fa-linkedin {
  background: var(--dark-grey);
  color: var(--light-grey);
}

.fa-github1 {
  background: var(--dark-grey);
  color: var(--light-grey);
}

.btn--outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary) !important;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: all .5s ease !important;
}

.btn--outline:hover {
  color: var(--light-grey) !important;
  background-color: var(--clr-primary);
}

@media screen and (max-width: 768px) {
  .page-size {
    height: 100vh;
    padding: 2rem 1.5rem;
    display: flex;
    align-items: center;
  }

  .title {
    padding: 0 0.5rem;
  }

  .title .home_name {
    font-size: min(13vw, 3rem);
    white-space: normal;
    text-align: center;
  }

  .title h2 {
    font-size: min(6vw, 1.8rem) !important;
    text-align: center;
  }

  .break {
    justify-content: center;
    text-align: center;
  }

  .fa {
    padding: 15px;
    font-size: 18px;
  }

  .home_btns {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    margin-top: 1.5rem;
  }

  .home_btns .btn--outline {
    width: 100%;
    max-width: 250px;
    text-align: center;
    display: block;
    padding: 0.9em 1.5em;
  }

  .ml-4 {
    margin-left: 0 !important;
  }
}

/* About Section */
.about-avatar img {
  width: 400px;
  height: 400px;
  max-width: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  display: block;
  margin: 0 auto;
}

.about-section {
  background-color: var(--clr-primary);
  padding-top: 3vh;
  padding-bottom: 3vh;
}

.about-section h6 {
  font-size: 1.15rem;
}

.about-section p {
  font-size: 1.05rem;
  line-height: 1.7;
}

.theme-color {
  color: var(--dark-grey);
}

.dark-color {
  color: var(--clr-primary);
}

.light-color {
  color: var(--light-grey);
}

.white-color {
  color: #fff;
}

.medium-color {
  color: var(--grey)
}

@media (max-width: 991px) {
  .about-avatar {
    margin-top: 30px;
  }
}

@media screen and (max-width: 768px) {
  .about-section {
    padding: 3rem 1rem;
  }

  .about-section .row {
    flex-direction: column;
  }

  .about-section h3 {
    font-size: 1.75rem;
    text-align: center;
  }

  .about-section h6 {
    font-size: 1.2rem;
    text-align: center;
    font-weight: 600;
  }

  .about-section p {
    font-size: 1.15rem;
    line-height: 1.75;
    text-align: left;
  }

  .col-lg-6 {
    width: 100%;
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 600px) {
  .about-avatar {
    text-align: -webkit-center;
    margin-bottom: 2rem;
  }
  
  .about-avatar img {
    width: 250px;
    height: 250px;
  }
  
  .about-section h3 {
    margin-bottom: 1rem !important;
  }
  
  .about-section h6 {
    margin-bottom: 1rem !important;
  }
  
  .about-section p {
    font-size: 1.1rem !important;
    line-height: 1.7 !important;
  }
  
  .aboutMe {
    text-align: center;
  }
}

/* Experience Section */
#experience {
  margin-top: 5vh;
}

.secondary-title {
  font-weight: 600;
  line-height: 1.4;
  font-size: 1.25rem;
  margin-top: 0;
}

#experience a {
  transition: all .5s ease !important;
}

#experience a:hover {
  background-color: var(--light-grey);
}

#education {
  margin-top: 5vh !important;
}

@media screen and (max-width: 768px) {
  #experience {
    margin-top: 3vh;
    padding: 0 0.5rem;
  }

  #experience h3 {
    font-size: 1.75rem;
  }

  .secondary-title {
    font-size: 1.15rem;
    line-height: 1.3;
    font-weight: 600;
  }

  #experience .grid {
    display: flex;
    flex-direction: column;
  }

  #experience time {
    font-size: 1.05rem;
    margin-bottom: 0.5rem;
    display: block;
  }

  #experience p {
    font-size: 1.1rem;
    line-height: 1.7;
    margin-left: 0 !important;
  }

  #experience .ml-8 {
    margin-left: 0 !important;
  }

  #experience article {
    padding: 1rem !important;
    margin-bottom: 0.5rem;
  }

  #experience ul {
    padding-left: 0;
  }
}

@media screen and (max-width: 480px) {
  #experience h3 {
    font-size: 1.5rem;
  }

  .secondary-title {
    font-size: 1.1rem;
  }

  #experience time {
    font-size: 1rem;
  }

  #experience p {
    font-size: 1.05rem;
    line-height: 1.65;
  }
}

/* Projects Section */
#projects {
  margin-top: 5vh;
  overflow: hidden;
}

.section__title {
  text-align: center;
  margin-bottom: 1.5em;
}

.projects__carousel-container {
  position: relative;
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 3.5rem;
}

.projects__carousel-wrapper {
  overflow-x: auto;
  scroll-behavior: smooth;
  scrollbar-width: none;
  -ms-overflow-style: none;
  padding: 1rem 0 2rem 0;
}

.projects__carousel-wrapper::-webkit-scrollbar {
  display: none;
}

.projects__carousel {
  display: flex;
  gap: 2rem;
  padding: 0.5rem;
}

.project {
  min-width: 350px;
  max-width: 350px;
  min-height: 480px;
  max-height: 480px;
  padding: 2rem;
  text-align: center;
  border: 1px solid #e0e0e0;
  transition: transform .2s linear, border-color .2s linear;
  flex-shrink: 0;
  background: white;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project:hover {
  transform: translateY(-5px);
  border-color: var(--clr-primary);
}

.project__title {
  font-size: 1.35rem;
  color: var(--dark-grey);
  margin-bottom: 0;
  font-weight: 700;
  min-height: 2.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project__description {
  margin-top: 0.75em;
  margin-bottom: 0.75em;
  color: var(--grey);
  font-size: 0.95rem;
  line-height: 1.6;
  flex-grow: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
}

@media screen and (max-width: 768px) {
  .project__description {
    font-size: 1.05rem;
  }
}

@media screen and (max-width: 480px) {
  .project__description {
    font-size: 1rem;
  }
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0.75em 0;
  gap: 0.4rem;
}

.project__stack-item {
  margin: 0;
  font-weight: 500;
  font-size: 0.85rem;
  color: var(--grey);
  background: var(--light-grey);
  padding: 0.35em 0.8em;
  border-radius: 4px;
}

.project .link--icon {
  margin-left: .5em;
}

.MuiSvgIcon-root {
  fill: var(--clr-primary);
  width: 24px;
  height: 24px;
  display: inline-block;
  font-size: 1.5rem;
  flex-shrink: 0;
  user-select: none;
  transition: all .5s ease !important;
}

.MuiSvgIcon-root:hover {
  fill: var(--dark-grey);
}

.carousel__nav-btn {
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  background: var(--clr-primary);
  color: white;
  border: none;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s ease;
  font-size: 1.3rem;
  box-shadow: var(--shadow);
  font-weight: bold;
}

.carousel__nav-btn:hover {
  background: var(--dark-grey);
  transform: translateY(-50%) scale(1.1);
}

.carousel__nav-btn:active {
  transform: translateY(-50%) scale(0.95);
}

.carousel__nav-btn--left {
  left: 0;
}

.carousel__nav-btn--right {
  right: 0;
}

.carousel__indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1rem;
}

.carousel__indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--grey);
  opacity: 0.4;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  padding: 0;
}

.carousel__indicator.active {
  background: var(--clr-primary);
  opacity: 1;
  width: 28px;
  border-radius: 5px;
}

@media screen and (max-width: 768px) {
  #projects {
    margin-top: 3vh;
    padding: 0 0.5rem;
  }

  .projects__carousel-container {
    padding: 0 2.8rem;
  }

  .project {
    min-width: 300px;
    max-width: 300px;
    min-height: 460px;
    max-height: 460px;
    padding: 1.75rem;
  }

  .carousel__nav-btn {
    width: 38px;
    height: 38px;
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 480px) {
  .projects__carousel-container {
    padding: 0 2.5rem;
  }

  .project {
    min-width: 280px;
    max-width: 280px;
    min-height: 450px;
    max-height: 450px;
    padding: 1.5rem;
  }

  .carousel__nav-btn {
    width: 35px;
    height: 35px;
    font-size: 1rem;
  }
}

/* Skills Section */
#skills {
  padding: 5rem 0;
}

#skills .container1 {
  max-width: 900px;
}

.skills__category-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-top: 2.5rem;
}

.skills__category {
  background: white;
  padding: 1.5rem;
  border-radius: 6px;
  border: 1px solid #e0e0e0;
  transition: transform 0.2s ease, border-color 0.2s ease;
}

.skills__category:hover {
  transform: translateY(-3px);
  border-color: var(--clr-primary);
}

.skills__category-title {
  color: var(--dark-grey);
  font-size: 1.1rem;
  font-weight: 700;
  margin: 0 0 1.25rem 0;
  padding-bottom: 0.75rem;
  letter-spacing: 0.3px;
  border-bottom: 2px solid var(--clr-primary);
}

.skills__list {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
  justify-content: flex-start;
  padding: 0;
  margin: 0;
  list-style: none;
}

.skills__list-item {
  padding: 0.6em 1.2em;
  font-size: 0.95rem;
  margin: 0;
  background: var(--light-grey);
  border-radius: 20px;
  border: 1px solid transparent;
  font-weight: 500;
  letter-spacing: 0.2px;
  transition: all 0.2s ease;
  color: var(--dark-grey);
}

.skills__list-item:hover {
  background: var(--clr-primary);
  color: white;
  border-color: var(--clr-primary);
  transform: scale(1.05);
}

.btn--plain {
  color: var(--dark-grey);
  box-shadow: none;
  border: 0;
  transition: all 0.2s ease;
}

@media screen and (max-width: 992px) {
  #skills .container1 {
    max-width: 85%;
  }
}

@media screen and (max-width: 768px) {
  #skills {
    padding: 4rem 0;
  }

  #skills .container1 {
    max-width: 90%;
  }

  .skills__category-wrapper {
    grid-template-columns: 1fr;
  }

  .skills__category {
    padding: 1.5rem;
  }

  .skills__category-title {
    font-size: 1.15rem;
  }

  .skills__list-item {
    font-size: 1rem;
    padding: 0.6em 1.1em;
  }
}

@media screen and (max-width: 480px) {
  #skills .container1 {
    max-width: 95%;
  }

  .skills__category {
    padding: 1.25rem;
  }

  .skills__category-title {
    font-size: 1.1rem;
  }

  .skills__list-item {
    font-size: 0.95rem;
    padding: 0.55em 1em;
  }
}

/* Footer */
#footer {
  margin-top: 7vh;
  background-color: var(--clr-primary);
}

.footer a {
  color: var(--light-grey);
  transition: all .5s ease !important;
}

.footer li a:hover {
  color: var(--dark-grey)
}

.ftr-btn {
  width: 3rem;
  height: 3rem;
  padding: 10px !important;
  display: flex !important;
  justify-content: center;
  align-items: center;
}

.ftr-btn:hover {
  color: var(--clr-primary)
}

.disabled {
  pointer-events: none;
  cursor: revert;
}
